// app/programs/[slug]/CredentialsDisplay.tsx
"use client"

import { useState } from "react"
import { Star } from "lucide-react"

interface Credential {
  title: string
  description: string
  skills: string
  rating: number
  reviews: number
  level: string
}

interface Props {
  credentials?: Credential[]
}

export default function CredentialsDisplay({ credentials = [] }: Props) {
  const [selectedLevel, setSelectedLevel] = useState("Beginner")
  const filteredCredentials = credentials.filter(c => c.level === selectedLevel)

  return (
    <>
      {/* Level Filter */}
      <div className="flex flex-wrap gap-3 mb-12">
        {["Beginner", "Intermediate", "Advanced"].map(level => (
          <button
            key={level}
            onClick={() => setSelectedLevel(level)}
            className={`px-4 py-2 rounded-full font-semibold transition-all ${
              selectedLevel === level
                ? "bg-foreground text-background"
                : "bg-foreground/10 text-foreground hover:bg-foreground/20"
            }`}
          >
            {level}
          </button>
        ))}
      </div>

      {/* Credentials List */}
      {filteredCredentials.length > 0 ? (
        filteredCredentials.map((c, idx) => (
          <div key={idx} className="mb-8 border border-border rounded-2xl p-4 md:p-8 bg-card">
            <h3 className="font-bold text-xl md:text-2xl mb-2">{c.title}</h3>
            <p className="text-foreground/70 mb-4">{c.description}</p>
            <p className="text-sm font-semibold text-foreground/60 mb-2">Skills you'll gain:</p>
            <p className="text-foreground/70 mb-4">{c.skills}</p>
            <div className="flex items-center gap-2 mb-4">
              <Star size={18} className="fill-yellow-400 text-yellow-400" />
              <span className="font-semibold">{c.rating}</span>
              <span className="text-foreground/60 text-sm">({c.reviews} reviews)</span>
            </div>
          </div>
        ))
      ) : (
        <p className="text-center text-foreground/60 py-12">
          No credentials available for this level.
        </p>
      )}
    </>
  )
}
